<template>
  <div>
    <el-form :inline="true" :model="seasonForm" class="demo-form-inline">
      <el-form-item label="赛季名">
        <el-input v-model="seasonForm.name" placeholder="输入赛季名"></el-input>
      </el-form-item>
      <el-form-item label="开始时间">
        <el-input v-model="seasonForm.opentime"></el-input>
      </el-form-item>
      <el-form-item label="报名是否结束">
        <el-radio-group v-model="seasonForm.is_over">
          <el-radio :label="true">已结束</el-radio>
          <el-radio :label="false">未结束</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="pushNewEvents">发布</el-button>
      </el-form-item>
    </el-form>
    <!-- 表格 -->
    <el-table v-loading="loading" :data="events" border height="50vh">
      <el-table-column prop="id" label="序号" width="60"> </el-table-column>
      <el-table-column label="名字" width="180">
        <template slot-scope="scope">
          <span
            v-if="!scope.row.isEdit"
            @dblclick="toggleIsEdit(scope.row.isEdit, scope.$index)"
            >{{ scope.row.name }}</span
          >
          <el-input
            v-else
            v-model="scope.row.name"
            @blur="toggleIsEdit(scope.row.isEdit, scope.$index)"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column label="是否结束" width="180">
        <template slot-scope="scope">
            <el-switch
              v-model="scope.row.is_over"
              active-color="#ff4949"
              inactive-color="#409EFF"
              active-text="已结束"
              inactive-text="进行中"
            >
            </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="开始时间" width="250">
        <template slot-scope="scope">
          <template v-if="scope.row.opentime === null">
            <el-input v-model="scope.row.opentime"></el-input>
          </template>
          <template v-else>
            <span
              v-if="!scope.row.isEdit"
              @dblclick="toggleIsEdit(scope.row.isEdit, scope.$index)"
              >{{ scope.row.opentime }}</span
            >
            <el-input
              v-else
              v-model="scope.row.opentime"
              @blur="toggleIsEdit(scope.row.isEdit, scope.$index)"
            ></el-input>
          </template>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="auto">
        <template slot-scope="scope">
          <el-button type="warning" size="mini" @click="updateItem(scope.row)"
            >更新</el-button
          >
          <el-button
            type="danger"
            size="mini"
            @click="deleteItem(scope.row.name)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {
  pushNewEvents,
  getAllEvents,
  deleteEvents,
  updateEvents,
} from "@/api/gameSeason/index";
export default {
  name: "SetSeason",
  data() {
    return {
      seasonForm: {
        name: "",
        is_over: false,
        opentime: "xxxx-xx-xxT00:00:00",
      },
      events: [],
      loading: false,
    };
  },
  mounted() {
    this.initEvents();
  },
  methods: {
    pushNewEvents() {
      pushNewEvents(
        this.seasonForm.name,
        this.seasonForm.is_over,
        this.seasonForm.opentime
      )
        .then((res) => {
          this.$message.success(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    initEvents() {
      this.loading = true;
      getAllEvents()
        .then((res) => {
          let { data } = res;
          this.events = data;
          for (var i = 0; i < data.length; i++) {
            const source = {
              isEdit: false,
            };
            this.events[i] = Object.assign(this.events[i], source);
          }
          console.log("🐕", this.events);
          setTimeout(() => {
            this.loading = false;
          }, 800);
        })

        .catch(() => {
          this.$message.error("服务器异常，请联系管理员！");
          setTimeout(() => {
            this.loading = false;
          }, 800);
        });
    },
    deleteItem(name) {
      this.$confirm("是否删除赛季?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          deleteEvents(name)
            .then(() => {
              this.$message.success("删除成功！");
              this.initEvents();
            })
            .catch(() => {
              this.$message.error("服务器异常，请联系管理员！");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    toggleIsEdit(isEdit, index) {
      this.events[index].isEdit = !isEdit;
      this.events = JSON.parse(JSON.stringify(this.events));
    },
    updateItem(row) {
      updateEvents(row.name, row)
        .then(() => {
          this.$message.success("更新成功！");
          this.initEvents();
        })
        .catch(() => {
          this.$message.error("服务器异常！");
        });
    },
  },
};
</script>

<style scoped lang="less"></style>
